<template>
  <div
    data-app="true"
    class="v-application v-application--is-ltr theme--light"
    id="app"
  >
    <div
      data-v-3b4096ca=""
      class="container homepage ma-0 pa-0 container--fluid app"
    >
      <!-- <AppHeader /> -->

      <MainSection class="section" id="section1" />
      <AboutPage class="section" id="section2" />
      <PortfolioPage class="section" id="section3" />
      <!-- 다른 섹션들을 동일한 방식으로 추가하세요 -->
    </div>

    <ul class="slide-menu">
      <li v-for="item in menuItems" :key="item.id">
        <a
          :href="`#${item.id}`"
          :class="{ active: activeMenuItem === item.id }"
          @click.prevent="handleClick(item.id)"
          >{{ item.label }}</a
        >
      </li>
    </ul>

    <div class="circle"></div>
    <div class="circle-2"></div>
  </div>
</template>

<script>
import MainSection from "./components/mainSection.vue";
import AboutPage from "./components/AboutPage.vue";
import PortfolioPage from "./components/PortfolioPage.vue";
import { ref, onMounted } from "vue";
import { scrollTo } from "vue-scrollto";

export default {
  name: "App",
  components: {
    MainSection,
    AboutPage,
    PortfolioPage,
  },
  setup() {
    const menuItems = [
      { id: "section1", label: "Intro" },
      { id: "section2", label: "Career" },
      { id: "section3", label: "Portfolio" },
    ];
    const activeMenuItem = ref(menuItems[0].id);

    const handleScroll = () => {
      const sections = document.querySelectorAll(".container .section");
      const scrollPosition = window.pageYOffset;

      sections.forEach((section) => {
        const sectionTop = section.offsetTop;
        const sectionBottom = sectionTop + section.offsetHeight;

        if (scrollPosition >= sectionTop && scrollPosition < sectionBottom) {
          const sectionId = section.getAttribute("id");
          activeMenuItem.value = sectionId;
          const activeMenuIndex = menuItems.findIndex(
            (item) => item.id === sectionId
          );
          if (activeMenuIndex >= 0) {
            const activeMenuLink =
              document.querySelectorAll(".slide-menu li a")[activeMenuIndex];
            document
              .querySelectorAll(".slide-menu li a.active")
              .forEach((el) => el.classList.remove("active"));
            activeMenuLink.classList.add("active");
          }
        }
      });
    };

    const handleClick = (id) => {
      const element = document.getElementById(id);
      scrollTo(element, {
        duration: 500,
        easing: "ease",
        offset: -0,
      });
      activeMenuItem.value = id;
      const activeMenuIndex = menuItems.findIndex((item) => item.id === id);
      if (activeMenuIndex >= 0) {
        const activeMenuLink =
          document.querySelectorAll(".slide-menu li a")[activeMenuIndex];
        document
          .querySelectorAll(".slide-menu li a.active")
          .forEach((el) => el.classList.remove("active"));
        activeMenuLink.classList.add("active");
      }
    };

    onMounted(() => {
      // 스크롤 위치가 변할 때마다 handleScroll 함수를 실행합니다.
      window.addEventListener("scroll", handleScroll);

      const menuLinks = document.querySelectorAll(".slide-menu li a");

      function onScroll() {
        const scrollPosition = window.pageYOffset;
        const sections = document.querySelectorAll(".container .section");

        sections.forEach((section, index) => {
          const sectionTop = section.offsetTop;
          const sectionBottom = sectionTop + section.offsetHeight;

          if (scrollPosition >= sectionTop && scrollPosition < sectionBottom) {
            activeMenuItem.value = section.getAttribute("id");
            menuLinks.forEach((link) => link.classList.remove("active"));
            menuLinks[index].classList.add("active");
          }
        });
      }

      // 페이지가 로드되면 스크롤 위치를 감지합니다.
      onScroll();
    });

    return {
      menuItems,
      activeMenuItem,
      handleClick,
    };
  },
};
</script>

